@font-face {
    font-family: "GreatVibes";
    src: url("../fonts/GreatVibes.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "JetBrainsMono";
    src: url("../fonts/JetBrainsMono.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

.confirmed-arrive {
    display: flex;  
    width: 80vw;
    height: auto;
    margin-inline: 10vw auto;
    margin-block: -30px;
    border-radius: 30px;
    padding-block: 90px;
    background-color: #FFFFFF;
    flex-direction: column;
}

.confirmed-arrive-round {
    width: 2vw;
    height: 2vw;
    border-radius: 1vw;
    background-color: #919682;
    margin-left: 50%;
}

.confirmed-arrive-round:last-child {
    margin-bottom: -2vw;
}

.confirmed-arrive-content {
    display: flex;
    flex-direction: row;
}

.confirmed-arrive-left {
    width: calc(50% + 1vw);
    height: auto;
    border-right: 3px solid #919682;
}

.confirmed-arrive-left-content {
    width: 120%;
    height: auto;
    margin-left: 40%;
    margin-block: 10vw;
    padding-block: 2vw;
    border-radius: 30px;
    border: 1px solid #919682;
    background-color: #FFFFFF;
    z-index: 2;
  }
  
.confirmed-arrive-left-content>h2 {
    font-family: "JetBrainsMono";
    text-align: center;
    font-size: 3vw;
    margin-block: 2vw;
    color: #919682;
    white-space: pre;
    margin-bottom: 3vw;
}

.confirmed-arrive-left-content>p {
    font-family: "GreatVibes";
    margin-bottom: 5vw;
    margin-inline: 3vw;
    line-height: 4vw;
    text-align: center;
    font-size: 2.5vw;
    color: #919682;
}

.confirmed-arrive-left-content-btn {
    position: relative;
    display: flex;
    font-family: "JetBrainsMono";
    color: #919682;
    justify-content: center;
    align-items: center;
    width: 80%;
    height: 3vw;
    border: 1px solid #919682;
    left: 10%;
    z-index: 3;
}

.confirmed-arrive-left-content-btn:hover {
    opacity: .5;
    cursor: pointer;
}

.confirmed-arrive-right {
    width: calc(50% - 1vw);
    height: auto;
}

@media (max-width: 768px) {
    .confirmed-arrive {
        width: 90%;
        margin-inline: 5% auto;
    }

    .confirmed-arrive-left {
        border-right: 2px solid #919682 /*#919682*/;
    }

    .confirmed-arrive-left {
        width: calc(50% + 0.8vw);
    }

    .confirmed-arrive-left-content {
        width: 180%;
        margin-left: 8%;
        padding-block: 4vw;
      }
      
    .confirmed-arrive-left-content>h2 {
        font-size: 5vw;
        margin-block: 2vw;
        color: #919682;
        margin-top: 4vw;
        margin-bottom: 8vw;
    }
    
    .confirmed-arrive-left-content>p {
        margin-bottom: 12vw;
        margin-inline: 3vw;
        line-height: 6vw;
        font-size: 5vw;
    }
    
    .confirmed-arrive-left-content-btn {
        width: 80%;
        height: 6vw;
        left: 10%;
        z-index: 3;
        margin-bottom: 3vw;
    }
}