@font-face {
    font-family: "GreatVibes";
    src: url("../fonts/GreatVibes.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
}

@font-face {
    font-family: "JetBrainsMono";
    src: url("../fonts/JetBrainsMono.ttf") format("truetype");
    font-weight: normal;
    font-style: normal;
  }

.dress-code {
    display: flex;  
    width: 80vw;
    height: auto;
    margin-inline: 10vw auto;
    margin-block: -30px;
    border-radius: 30px;
    background-color: #595E48;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center; 
    z-index: 2;
}

.dress-code>h2 {
    font-family: "JetBrainsMono";
    font-size: 3vw;
    color: #FFFFFF;
    white-space: pre;
    margin-block: 2vw -2vw;
}

.dress-code-content {
    font-family: "GreatVibes";
    color: #ECE1DD;
    font-size: 3vw;
    border: 2px solid #919682;
    border-radius: 30px;
    margin-block: 4vw 5vw;
    padding-block: 4vw;
    padding-inline: 13vw;
}

.dress-code-content>h3 {
    font-family: "GreatVibes";
    color: #ECE1DD;
    font-size: 8vw;
    margin-block: 1vw;
}

.dress-code-colors {
    display: flex;
    width: auto;
    height: auto;
    margin-block-start: 4vw;
    column-gap: 2vw;
}

.dress-code-colors>div {
    width: 7vw; 
    height: 7vw;
    border: 2px solid #919682; 
    border-radius: 3.5vw;
}

@media (max-width: 768px) {
    .dress-code {
        width: 90%;
        margin-inline: 5% auto;
    }

    .dress-code>h2 {
        font-size: 5vw;
        font-weight: normal;
        margin-block: 4vw 0;
        white-space: " ";
    }

    .dress-code-content {
        font-size: 5vw;
        margin-block: 4vw 5vw;
        padding-block: 4vw;
        padding-inline: 2vw;
        border: 1px solid #919682;
        line-height: 6vw;
    }

    .dress-code-content>h3 {
        font-size: 14vw;
        margin-block: 4vw;
    }

    .dress-code-colors {
        margin-block: 2vw 1vw;
        margin-inline-start: 2.5vw;
        column-gap: 3vw;
    }
    
    .dress-code-colors>div {
        width: 10vw; 
        height: 5vw;
        border: 1px solid #919682; 
        border-radius: 0vw;
    }
}