@font-face {
  font-family: "JetBrainsMono";
  src: url("../fonts/JetBrainsMono.ttf") format("truetype");
  font-weight: normal;
  font-style: normal;
}

.slider-place {
  display: flex;
  width: 80vw;
  height: auto;
  margin-inline: 10vw auto;
  margin-bottom: 2vw;
  /* margin-top: 1vw; */
  padding-top: 3vw;
  background-color: #ffffff;
  flex-direction: column;
  text-align: center;
}

.slider-place>h2 {
  font-family: "JetBrainsMono";
  font-size: 3vw;
  color: #919682;
  white-space: pre;
  margin-bottom: 1.5vw;
}

.slider-place-adress {
  display: flex;
  height: auto;
  /* margin-inline: 10vw auto; */
  border-radius: 30px;
  flex-direction: row;
}

.slider-place-adress-info {
  width: 50%;
  height: auto;
  background-color: #595e48;
}

.slider-place-adress-info>h2 {
  font-family: "JetBrainsMono";
  font-size: 2.5vw;
  margin-block: 2vw;
  color: #FFFFFF;
  white-space: pre;
  margin-bottom: 1.5vw;
}

.slider-place-adress-info>p {
  font-family: "JetBrainsMono";
  margin-inline: 3vw;
  text-align: left;
  font-size: 1.3vw;
  color: #FFFFFF;
}

.slider-place-open-btn {
  display: flex;
  font-family: "JetBrainsMono";
  color: #FFFFFF;
  justify-content: center;
  align-items: center;
  width: 80%;
  height: 3vw;
  border: 1px solid #FFFFFF;
  margin: 6vw auto auto 10%;
}

.slider-place-open-btn:hover {
  opacity: .5;
  cursor: pointer;
}

.slider-place-map {
  width: 50%;
  height: 20vw;
}

.slider-place-content {
  display: flex;
  width: 100%;
  height: auto;
  justify-content: center;
  align-items: center;
  text-align: center;
  overflow: hidden;
}

.slider-image {
  width: 100%;
  height: auto;
  border-radius: 30px 30px 0 0;
}

.slider-btn-prev,
.slider-btn-next {
  width: 8vw;
  height: 4vw;
  border: 1px solid #595e48;
  background-color: #595e48;
  color: #ece1dd;
  z-index: 2;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
}

.slider-btn-prev {
  margin-right: -4vw;
  border-radius: 15px 0 0 15px;
}

.slider-btn-prev > img,
.slider-btn-next > img {
  width: 1.5vw;
  height: auto;
}

.slider-btn-next {
  margin-left: -4vw;
  border-radius: 0 15px 15px 0;
}

.slider-btn-prev:hover,
.slider-btn-next:hover {
  background-color: #404238;
  border: 1px solid #404238;
}

@media (max-width: 768px) {
  .slider-place {
    width: 90%;
    padding-bottom: 10vw;
    margin-inline: 5% auto;
    margin-top: -5vw;
    margin-bottom: -0.1vw;
    overflow: hidden;
  }

  .slider-place>h2 {
    font-size: 5vw;
    font-weight: normal;
  }

  .slider-place-adress {
    display: flex;
    height: auto;
    flex-direction: column-reverse;
    margin-bottom: -10vw;
  }
  
  .slider-place-adress-info {
    width: 100%;
    height: 50vw;
    background-color: #595e48;
  }
  
  .slider-place-adress-info>h2 {
    width: 100%;
    text-align: center;
    font-size: 5vw;
    color: #FFFFFF;
    margin-bottom: 10%;
  }
  
  .slider-place-adress-info>p {
    text-align: center;
    width: 60%;
    margin-left: 20%;
    font-size: 3.2vw;
    line-height: 4vw;
  }

  .slider-place-open-btn {
    width: 80%;
    height: 6vw;
    border: 1px solid #FFFFFF;
    margin: 5vw auto auto 10%;
  }

  .slider-place-map {
    width: 100%;
    height: 50vw;
  }

  .slider-image {
    width: 120vw;
  }

  .slider-place-content {
    position: relative;
  }

  .slider-btn-prev,
  .slider-btn-next {
    width: 10vw;
    height: 10vw;
    border-radius: 5vw;
    position: absolute;
  }

  .slider-btn-prev > img,
  .slider-btn-next > img {
    width: 3vw;
    height: auto;
  }

  .slider-btn-prev {
    margin: 0;
    left: 7vw;
  }

  .slider-btn-next {
    margin: 0;
    right: 7vw;
  }
}
